@use 'global' as *;

.p-page-recruit {
    padding-top: rem(40);
    padding-bottom: rem(110);
    background-image: url(../images/common/bg-recruit.png);
    background-repeat: repeat;
    @include mq(md) {
        padding-top: rem(116);
    }
}
.p-page-recruit__title {
    color: #391E88;
    text-align: center;
    font-size: rem(24);
    font-weight: bold;
    margin-bottom: rem(30);
    @include mq(md) {
        font-size: rem(32);
    }
}

.p-page-recruit__main {
    font-size: rem(48);
    line-height: calc(56 / 48);
    letter-spacing: 0.01em;
    text-align: center;
    font-weight: bold;
    @include mq(md) {
        font-size: rem(72);
    }
}

.p-page-recruit__sub {
    letter-spacing: 0.05em;
    font-size: rem(18);
    margin-top: rem(15);
    text-align: center;
    @include mq(md) {
        font-size: rem(32);
    }

}

.p-page-recruit__text {
    text-align: center;
    font-size: rem(15);
    line-height: calc(27 / 15);
    margin-top: rem(25);
    @include mq(md) {
        font-size: rem(16);
    }
}
.p-page-recruit__image {
    margin-top: rem(40);
    width: 100vw;
    margin-left: calc(50% - 50vw);
}


.p-page-recruit__item {
    border: 1px solid #391E88;
    border-radius: 10px;
    padding: rem(30) rem(20);
    background-color: #fff;
}

.p-page-recruit__item:not(:first-child) {
    margin-top: rem(40);

}

.p-page-recruit__job-title {
    font-size: rem(16);
    font-weight: bold;
    letter-spacing: 0.05em;
    color: #391E88;
    text-align: center;
    @include mq(md) {
        font-size: rem(24);
    }
}

.p-page-recruit__items {
    margin-top: rem(30);

}

.p-page-recruit__recruit {
    margin-top: rem(40);
}

.p-page-recruit__apply {
    margin-top: rem(40);
    text-align: center;
}

.p-page-recruit__apply p {
    margin-top: rem(20);
    font-size: rem(12);
}

.p-page-recruit__movies {
    margin-top: rem(80);
    @include mq(md) {
        margin-top: rem(140);
    }
}
.p-page-recruit__movie {
    margin-top: rem(30);
    position: relative;
}

.p-page-recruit__movie:before {
    content: "";
    display: block;
    padding-top: 56.25%;

}
.p-page-recruit__movie iframe{
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    width: 100%;
    height: 100%;

}

